.bg-board-untamed {
  background: url(/20618d6ecde62cf7617a8191610a3c90.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  filter: none;
}
.flex-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.navbar-sm {
  min-height: 40px;
  height: 40px;
}
.navbar-sm .navbar-brand {
  padding: 0px 12px;
  font-size: 18px;
  line-height: 40px;
}
.navbar-sm .navbar-nav > li > a {
  padding-top: 0px;
  padding-bottom: 0px;
  line-height: 40px;
}
.navbar-sm .navbar-nav > li > span {
  padding-top: 0px;
  padding-bottom: 0px;
  line-height: 40px;
}
a {
  color: #5cb85c;
  text-decoration: none;
}
a:hover {
  color: #5bc0de;
  text-decoration: none;
}
.rbt-input > div {
  width: 100%;
}
.rbt-input > div > div {
  width: 100%;
}
.rbt-input > div > div > div {
  width: 100%;
}
.rbt-input > div > div > div > input {
  width: 100% !important;
}
.btn-transparent {
  background-color: transparent;
}
.wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
}
.content {
  padding: 10px;
  padding-top: 45px;
}
.navbar .container {
  padding-right: 0;
}
.navbar-nav li .glyphicon {
  padding-left: 5px;
  padding-right: 5px;
}
.pages-enter {
  opacity: 0;
}
.pages-enter.pages-enter-active {
  opacity: 1;
  transition: opacity 600ms ease-in-out;
}
.pages-leave {
  opacity: 0;
}
.pages-leave.pages-leave-active {
  opacity: 1;
  transition: opacity 600ms ease-in-out;
}
.rrt-message {
  background: #0E0B10 !important;
}
.rrt-confirm {
  background: #824d9e !important;
}
.no-padding {
  padding: 0;
}
.bold {
  font-weight: bold;
  color: white;
}
.slider.slider-horizontal {
  width: 100%;
}
.text-left {
  text-align: left !important;
}
.navbar-collapse {
  background-color: #222222;
  color: white;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.dropdown-menu > li > a:hover {
  color: #FFF;
  background: #2C2C2C;
}
.adaptive-short {
  background-color: #DEC052;
}
